import styles from "./Reviews.module.scss";
import CardReviews from "../CardReviews/CardReviews";
import { useState } from "react";

const usersData = [
  {
    id: 1,
    userName: " ",
    userPosition: " ",
    questions: [
      "",
      "",
      "",
    ],
    replies: [
      "NPO",
      "",
      "",
    ],
  },
  {
    id: 2,
    userName: "",
    userPosition: " ",
    questions: [
      "",
      "",
      "",
    ],
    replies: [
      "SNS",
      "",
      "",
    ],
  },
  {
    id: 3,
    userName: "",
    userPosition: " ",
    questions: [
      "",
      "",
      "",
    ],
    replies: [
      "",
      "",
      "",
    ],
  },
  {
    id: 4,
    userName: "",
    userPosition: " TBS",
    questions: ["", ""],
    replies: [
      "",
      "",
    ],
  },
];

const Reviews = () => {
  const [activeCardIndex, setActiveCardIndex] = useState(0);

  const handlePrevClick = () => {
    setActiveCardIndex((prevIndex) =>
      prevIndex > 0 ? prevIndex - 1 : usersData.length - 1
    );
  };
  const handleNextClick = () => {
    setActiveCardIndex((nextIndex) =>
      nextIndex < usersData.length - 1 ? nextIndex + 1 : 0
    );
  };

  return (
    <>
      <section className={styles.reviews}>
        <div className={styles.contaner}>
          <h2></h2>
          <div className={styles.card_container}>
            <button className={styles.button} onClick={handlePrevClick}>
              &#60;
            </button>
            <CardReviews {...usersData[activeCardIndex]} />
            <button className={styles.button} onClick={handleNextClick}>
              &#62;
            </button>
          </div>
        </div>
      </section>
    </>
  );
};

export default Reviews;
